import {
	api,
	postajax
} from '@/utils/api.js'
export default {

	data() {
		return {
			phone: '',
			pwd: ''
		}
	},

	created() {

	},
	methods: {
		submit: function() {
			var $me = this;
			var param = {
				username: $me.phone,
				password: $me.pwd
			}
			postajax(api.login, param).then(da => {
				console.log(da)
				if (da.code == 200) {
					wx.redirectTo({
						url: '/pages/index/index'
					});
					wx.setStorageSync("userinfo", da.data);
				} else {
					wx.showToast({
						title: da.msg,
						icon: 'none'
					})
				}
			})

		}
	}
}
